<template>
  <div class="df">
    <LeftTool @setting="getSetting" ref="left"></LeftTool>
    <div class="rightTool">
      <el-form v-if="settingItem&&settingItem.im.type=='form'" :model="settingItem.im.option">
        <el-form-item v-for="im in options" :label="im.title" :key="im.key">
          <component :is="'Auto' + im.type" v-model="im.option" @blur="blur"></component>
        </el-form-item>
      </el-form>
      <el-form v-if="settingItem&&settingItem.im.type==='chart'" :model="settingItem.im.option">
        <el-form-item>
          <ChartSetting v-model="settingItem.im.option" @update="update"></ChartSetting>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import LeftTool from '@/components/leftTool';
import handel from '@/components/tools/handelCom';
export default {
  name: 'autoForm',
  props: {},
  data() {
    return {
      settingItem: null,
      options: [],
      chart: ['row'],
    };
  },
  components: {
    LeftTool,
  },
  methods: {
    getSetting(e) {
      this.options = [];
      this.settingItem = e;
      Object.keys(e.im.option).map((i) => {
        let op = handel(i);
        op ? this.options.push({ ...op, option: { value: e.im.option[i] } }) : '';
      });
    },
    blur() {
      const { settingItem, options } = this;
      options.map((im) => {
        const { option, key } = im;
        settingItem.im.option[key] = option.value;
      });
      this.$refs.left.changeOption(settingItem);
    },
    update(res) {
      this.settingItem.im.option = res;
      this.$refs.left.changeOption( this.settingItem);
    },
  },
  mounted() {},
  computed: {},
  watch: {},
};
</script>

<style lang='scss' scoped>
.rightTool {
  position: fixed;
  width: 200px;
  right: 0;
  top: 0;
  bottom: 0;
  overflow: scroll;
}
</style>