<template>
  <div class="df main">
    <div class="w200">
      <h2>表单组件</h2>
      <div v-for="im in formTools" :key="im.componentName" class="model" draggable @dragstart="drag(im.componentName, '1')">
        {{ im.componentName }}
      </div>
      <h2>图形组件</h2>
      <div v-for="im in chartTools" :key="im.componentName" draggable class="model" @dragstart="drag(im.componentName, '2')">
        {{ im.componentName }}
      </div>
    </div>
    <div @drop="drop" class="view" @dragover.prevent>
      <Row :gutter="20">
        <Col :span="Number(im.option.span)" v-for="(im, ix) in form" :key="im.key">
        <div class="formItem" @dragstart="drag(im.componentName, '0', ix)" draggable @click="settingOption(im, ix)">
          <component :is="computedName(im.name)" @blur="settingOption(im, ix)" v-model="im.option"></component>
        </div>
        </Col>
      </Row>
    </div>
  </div>
</template>

<script>
import { debounce, throttle } from '../tools';
import { formSetting, chartSetting } from '../tools/setting'; //chartSetting
import { Row, Col } from 'element-ui';
export default {
  name: 'leftPage',
  props: {},
  data() {
    return {
      formTools: [
        {
          componentName: 'Input',
          componentIcon: '',
        },
        {
          componentName: 'Select',
          componentIcon: '',
        },
      ],
      chartTools: [
        {
          componentName: 'Sector',
          componentIcon: '',
        },
        {
          componentName: 'Histogram',
          componentIcon: '',
        },
      ],
      moveItem: null, //name:组件名  type:1(form移入)  0调换顺序 ，2chart移入
      form: [],
    };
  },
  components: { Row, Col },
  methods: {
    debounceFun(fun, param) {
      debounce(fun, param);
    },
    throttleFun(fun, param) {
      throttle(fun, param);
    },
    drag(name, type, index) {
      this.moveItem = { name, type, index };
    },

    computedName(name) {
      return this.chartTools.some((i) => i.componentName === name) ? 'AutoSector' : 'Auto' + name;
    },
    drop(e) {
      const { pageY: ot, pageX: ol } = e;
      let setting;

      if (this.moveItem?.type === '1' || this.moveItem?.type === '2') {
        const { type, name } = this.moveItem;
        type === '1' ? (setting = formSetting) : (setting = chartSetting);
        let index;
        document.querySelectorAll('.formItem').forEach((e, ix) => {
          const { offsetTop: top, clientHeight: height, offsetLeft: left, clientWidth: width } = e;
          if (top <= ot && top + height > ot && ol >= left && ol < left + width) {
            if (top <= ot && ot < top + (1 / 2) * height) {
              index = ix;
            }
            if (top + height > ot && ot >= top + (1 / 2) * height) {
              index = ix + 1;
            }
          } else if (top <= ot && top + height > ot && ol >= left + width) {
            index = ix + 1;
          }
        });
        if (index >= 0) {
          this.form.splice(index, 0, {
            name,
            type: type === '1' ? 'form' : 'chart',
            option: this.deepClone({
              ...(Array.isArray(setting[this.moveItem.name]) ? setting[this.moveItem.name][0] : setting[this.moveItem.name]),
              span: type === '1' ? 24 : 12,
            }),
            key: new Date().getTime() + index + '',
          });
        } else {
          this.form.push({
            name,
            type: type === '1' ? 'form' : 'chart',
            option: this.deepClone({
              ...(Array.isArray(setting[this.moveItem.name]) ? setting[this.moveItem.name][0] : setting[this.moveItem.name]),
              span: type === '1' ? 24 : 12,
            }),
            key: new Date().getTime() + '-1',
          });
        }
      }
      if (this.moveItem?.type === '0') {
        let index,
          length = 0;
        document.querySelectorAll('.formItem').forEach((e, ix) => {
          const { offsetTop: top, clientHeight: height, offsetLeft: left, clientWidth: width } = e;
          length++;
          if (top <= ot && ot < top + height && ol >= left && ol < left + width) {
            index = ix;
          } else if (top <= ot && top + height > ot && ol >= left + width) {
            index = ix + 1;
          }
        });
        index = index >= 0 ? index : length;
        const temp = this.form[this.moveItem.index];
        this.form.splice(this.moveItem.index, 1);
        if (this.moveItem.index >= index) {
          this.form.splice(index, 0, temp);
        } else {
          this.form.splice(index - 1, 0, temp);
        }
      }
      this.moveItem = null;
    },
    settingOption(im, ix) {
      this.$emit('setting', { im, ix });
    },
    deepClone(params) {
      return JSON.parse(JSON.stringify(params));
    },
    changeOption(e) {
      this.form[e.ix] = e.im;
    },
  },
  mounted() {},
  computed: {},
  watch: {},
};
</script>

<style lang='scss' scoped>
.model {
  padding: 4px;
  border: solid;
  display: block;
  display: inline-block;
  margin: 4px;
  cursor: pointer;
}
.w200 {
  user-select: none;
}
.view {
  width: calc(100% - 200px);
  padding-bottom: 40px;
  min-height: 800px;
}
.formItem {
  padding: 10px;
}
</style>