export default (type)=>{
    let result
    switch (type) {
        case 'span':
            result={type:'Input',title:'宽度'}
            break;
        case 'placeholder':
            result={type:'Input',title:'提示语'}
            break;
        case 'title':
            result={type:'Input',title:'标题'}
            break;
        case 'value':
            result={type:'Input',title:'值'}
            break;
        
        default:
            break;
    }
    return result?{...result,key:type}:result
}